
import XHeader from 'vux/src/components/x-header/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'

import Scroll from 'assembly/scroll/scroll'

export default {
data () {
return {
switchIndex: 0,
listenScroll: true,
weekHistory: [],
monthHistory: []
}
},
methods: {
switchTabItem (index) {
this.switchIndex = index
this.$refs.scroll.refresh()
},
toDetail (id, type) {
this.$router.push({
path: '/weekReportDetail',
query: {
id: id,
type: type
}
})
},
loadMore () {
},
_getWeekHistory () {
this.$tupHttp(this, 'front/report/weekly-history', 'POST').then(res => {
this.weekHistory = res.data.models
})
},
_getMonthHistory () {
this.$tupHttp(this, 'front/report/monthly-history', 'POST').then(res => {
this.monthHistory = res.data.models
})
}
},
created () {
this._getWeekHistory()
this._getMonthHistory()
},
components: {
XHeader,
Divider,
Tab,
TabItem,
Cell,
Group,
Scroll
}
}



